import React, {  Fragment } from "react";
import "./_toggleButtons.scss";
import { ToggleButton, ToggleButtonGroup } from "react-bootstrap";

export default function MultiToggleButtons({ input, options ,toggleColor }) {
  console.log(input.value);
  const handleValue = (e) => {
    // setValue(value);
    input.onChange(e);
    // console.log(value)
    console.log(e);
  };


  console.log(input);
  return (
    <Fragment>
      <ToggleButtonGroup
      className={"themeButton-toggle-group "+toggleColor }
        onChange={handleValue}
        type="radio"
        defaultValue={input?.value}
        name="options"
      >
        {options.map((item, idx) => (
          <ToggleButton value={item.value}>{item.label}</ToggleButton>
        ))}
      </ToggleButtonGroup>
    </Fragment>
  );
}
